@import (less) "color.less";

.iradio-container {
    margin-right: 0.625em;
}
.iradio-container.checked {
  .radio-label {
    color: @green !important;
  }
  .radio-label-icon {
    color: @green !important;
  }
  .radio-icon {
    color: @green !important;
  }
}
.iradio-container.disabled {
  .radio-label {
      opacity: 0.5;
    // color: @light_gray !important;
  }
}
.iradio_intesa {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    padding: 0;
    width: 22px;
    height: 22px;
    background: url(/cjlib/assets/images/intesa.png) no-repeat;
    border: none;
    background-position: -222px -1px;
    input[type="radio"] {
        margin: 4px 4px 0 0 !important;
        cursor: pointer;
        opacity: 0;
        &:disabled {
            cursor: auto;
        }
    }
    &.yellowborder {
        background-position: -294px -1px;
    }
}
.focusTabRadio {
    box-shadow: 0 0 0.05em 0.23em rgba(37,137,0,0.6) !important
}
.iradio_intesa_spunta.checked {
    background-position: -330px -1px !important;
    &.yellowborder {
        background-position: -617px -1px !important;
    }
}
.iradio_intesa_spunta.checked.disabled {
    opacity: 0.5;
    // background-position: -144px 0 !important;
    // &.yellowborder {
    //     background-position: -340px 0 !important;
    // }
}
.iradio_intesa.checked {
    background-position: -186px -1px;
    &.yellowborder {
        background-position: -258px -1px ;
    }
}
.iradio_intesa.disabled {
    opacity: 0.5;
    // background-position: -72px 0;
    // cursor: default;
    // &.yellowborder {
